<template>
<div style="width:100% heigth:100%">
  <q-card class="q-ma-sm">
      <q-card-section :class="`bg-${selecionada.color || 'green'}  text-white no-margin`" >
          <q-icon v-if="!this.selecionada" name="monetization_on" size="25px"></q-icon>
          <cryptoicon v-else :symbol="`${selecionada.icon}`" size="25" />
          {{selecionada.label || 'Escolha a moeda'}}
        </q-card-section>

      <q-card-section>
        <q-select filled v-model='selecionada' :options='moedas' label='Criptomoeda' style="width:100%"/>
      </q-card-section>

    <q-card-section class="no-padding centralizar">
        <h3 v-if="!dadosVazios">{{dados.ticker.buy | toBRL}}</h3>
    </q-card-section>

      <q-card-section v-if="!dadosVazios" class="row">
          <p style="color:green" class="col-6 centralizar"><q-icon name="trending_up" size="25px"/> Maior alta 24h: {{dados.ticker.high | toBRL}}</p>
          <p style="color:red" class="col-6 centralizar"><q-icon name="trending_down" size="25px"/> Maior baixa 24h: {{dados.ticker.low | toBRL}}</p>
      </q-card-section>

    <q-toggle v-model="criarAlerta" label="Adicionar alerta"/>
    <q-card-section v-if="criarAlerta" class="q-mb-md">
        <q-input filled v-model='valorAlerta' style="width:100%" label="Valor:" prefix="R$" class="centralizar q-mb-sm" />
        <q-btn class="justify-end" color="primary" label="Criar" @click="criarNotificacao"/>
        <q-btn class="justify-end" color="warning" label="Cancelar" @click="cancelarNotificacao"/>
    </q-card-section>

      <q-btn color="secondary full-width" @click="getDados">Ok</q-btn>
  </q-card>
</div>
</template>

<script>
export default {
mounted(){

},

 data(){
    return {
        moedas: [
            {
                label:'Bitcoin',
                value:'BTC',
                color: 'yellow',
                icon:'btc'
            },{
                label:'Ethereum',
                value:'ETH',
                color:'black',
                icon:'eth'
            },{
                label:'ChainLink',
                value:'LINK',
                color: 'red',
                icon:'link'
            },{
                label:'XRP',
                value:'XRP',
                color:'blue',
                icon:'xrp'
            }
            ],
        selecionada: '',
        dados: [],
        criarAlerta: false,
        alertaCriado: false,
        valorAlerta:'',
        alertarEm:''
    }
 },
 filters:{
     toBRL: function(value){
         const valueNum = parseFloat(value)
         return valueNum.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })
     }
 }, 

 computed:{
        dadosVazios(){
        if(this.dados == ''){
            return true
        } return false
    },
 },


 methods:{
     cancelarNotificacao(){
        this.criarAlerta = false
        this.alertaCriado = false
        this.valorAlerta = ''
        this.alertarEm = ''
     },
     criarNotificacao(){
        this.alertarEm = this.valorAlerta
        this.alertaCriado = true
        this.$q.notify({
                    color:'orange',
                    message:`Alerta ${this.selecionada.label} em ${this.paraReal(this.alertarEm)} criado.`,
                    title:`Criado`,
                    progress:true,
                    position:'top-right',
                    timeout:5000})
     },
    getDados(){
        this.$http.get(`/${this.selecionada.value}/ticker`)
        .then((response)=>{
            this.dados = response.data
            this.atualizaDados()
            this.$q.notify({
                    color:'green',
                    message:`Iniciando monitor ${this.selecionada.label}.`,
                    title:`Criado`,
                    progress:true,
                    position:'top-right',
                    timeout:5000})
        }).catch((error)=>{
            console.log('Um erro foi encontrado'+ error)
        })
    },

    atualizaDados(){
        setInterval(() => {
            this.$http.get(`/${this.selecionada.value}/ticker`)
        .then((response)=>{
            this.dados = response.data
            const valorAtual = this.dados.ticker.buy
        if(this.criarAlerta && this.alertaCriado){
            if(valorAtual >= this.alertarEm){
                this.$q.notify({
                    color:'green',
                    message:`${this.selecionada.label} chegou ao valor de ${this.paraReal(valorAtual)}.`,
                    progress:true,
                    title:`${this.selecionada.label}`,
                    position:'top-right',
                    timeout:5000})
            }
        }
        }).catch((error)=>{
            console.log('Um erro foi encontrado '+ error)
                this.$q.notify({
                color:'red',
                message:`${this.selecionada.label} gerou um erro ${error.message}`,
                progress:true,
                title:`${this.selecionada.label}`,
                position:'top-right',
                timeout:5000})
        })
        }, 10000);
    },

    paraReal(value){
        const valor = parseFloat(value)
        return "R$ " + valor.toFixed(2).replace('.', ',').replace(/(\d)(?=(\d{3})+,)/g, "$1.")
    }
}
}
</script>

<style>
.centralizar{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
</style>